export const required = value => {
    if (value) {
        return undefined
    } else if (!value) {
        return " ' "
    }
}

export const maxLenghtCrater = (maxLenght) => value => {
    if (value.length > maxLenght || isNaN(Number(value))) {
        return `   `
    }
    return undefined

}


export const nameCreate = value => {
    if (Number.isInteger(value) == false) {
        return ` '`
    }
    return undefined

}
