const newSnekReducer = (state = initialState, action) => {
    switch (action.type) {
        case NEWSNEK: {
            return {
                ...state,
                snek: [...state.snek, ...action.snek]
            }
        }

        default:
            return state;

    }

}

export const newsnekAC = (users) => {
    return {
        type: NEWSNEK,
        users: users,
    }
}

export default newSnekReducer;