const sneksReducer = (state = initialState, action) => {
    switch (action.type) {
        case SETSNEKS: {
            return {
                ...state,
                pizza: [...state.sneks, ...action.sneks]
            }
        }

        default:
            return state;


    }


}


export const setsnekAC = (users) => {
    return {
        type: SETSNEKS,
        users: users,
    }
}

export default sneksReducer;